import React, { useEffect } from 'react'
import { useState } from 'react'
import MemberCard from '../components/MemberCard'
import AudioComp from '../components/AudioComp'
import EsLinks from '../components/EsLinks'
import MainAPI from '../assets/APIs/mainApi'
import SideImg from './SideImg'
// Images
import img from '../assets/img/chairman-sir1.webp'
import img_1 from "../assets/img/National-Helpline.jpg"
import img_2 from "../assets/img/mask-bd-portal-.jpg"
import axios from 'axios'

function SideBar() {
    const [mainData, setMainData] = useState(MainAPI.Straps);
    const updateList = mainData.filter(e => {
        return e.isSidebar === true
    })
    const API = `https://zpr.s3cbd.com/api/staff`
    // const API = `https://api.pujakaitem.com/api/products`

    const getAPI = async (api) => {
        try {
            const res = await axios.get(api);
            console.log(res.data);
        } catch (error) {
            console.log(error.message);
        }
    }
    useEffect(() => {
        getAPI(API)
    }, [])
    // const [apiData, setApiData] = useState(null)
    // console.log(apiData);
    // useEffect(() => {
    //     const fetchAPI = async () =>{
    //         const res = await fetch(API)
    //         const data = await res.json();
    //         setApiData(data)
    //     }
    //     fetchAPI()
    // }, [])

    return (
        <>
            <div className="sideBar">
                <div className="memberBox">
                    {
                        updateList.map(e => {
                            const { id, name, posi, photo } = e
                            return (
                                <>
                                    <MemberCard id={id} tittle={posi} img={`${photo}`} reg={name} />
                                </>
                            )
                        })
                    }
                </div>
                <AudioComp tittle={' '} />
                <SideImg tittle={'   '} img={img_2} />
                <SideImg tittle={' '} img={img_1} />
                <EsLinks />
            </div>
        </>
    )
}

export default SideBar
